<template>
  <div class="pem-anonymous-noread-list">
    <div class="pem-anonymous-noread-list-item" 
      v-for="(item, index) in list" 
      @click="toAnonymousDetail(item.anonymous_id)"  
      :key="index">
      <img class="pem-anonymous-noread-list-item-logo" :src="item.logo" @error="defaultLogo" />
      <div class="pem-anonymous-noread-list-item-info">
        <h3 class="name">{{ item.name }}</h3>
        <p v-if="item.comment" class="comment">{{ item.comment }}</p>
        <p class="createtime">{{ item.createtime}}</p>
      </div>
      <div class="pem-anonymous-noread-list-item-right">
        <p v-if="item.content" class="content">{{ item.content }}</p>
        <img v-if="!item.content" :src="item.image" alt="">
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import { mapState } from 'vuex'

export default {
  props: {
    list: {
      type: Array,
      default: () => []
    }
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    toAnonymousDetail (id) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this.$router.push(`/anonymous-new-detail/${id}`)
      }
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-default-avatar@2x.png')
    }
  }
}
</script>



