<template>
  <div class="my-anonymous full-screen">
    <div class="page-panel" :class="{'page-panel-clear': tabtype === 'notice'}">
        <pem-scroll-view ref="scroll"
          class="scroll-view"
          :probeType="3"
          :data="listenList"
          :pullup=true
          :pulldown=true
          @pullUpLoad="listMore"
          @pullDownRefresh="listRefresh">
          <div class="scroll-view-container">
            <!-- 我发布的 -->
            <div v-show="tabtype === 'publish'" class="my-list-wrapper">
              <div class="scroll-view-pulldown">下拉刷新...</div>
              <pem-anonymous-list :list="publishList" myanonymous/>
              <div class="scroll-view-pullup">{{ isMore ? '加载中...' : '目前就这么多了' }}</div>
            </div>
            <!-- 我参与的 -->
            <div v-show="tabtype === 'join'" class="my-list-wrapper">
              <div class="scroll-view-pulldown">下拉刷新...</div>
              <pem-anonymous-list :list="joinList"/>
              <div class="scroll-view-pullup">{{ isMore ? '加载中...' : '目前就这么多了' }}</div>
            </div>
            <!-- 历史通知 -->
            <div v-show="tabtype === 'notice'" class="my-list-wrapper">
              <div class="scroll-view-pulldown">下拉刷新...</div>
              <pem-anonymous-noread-list :list="noticeList"/>
              <div class="scroll-view-pullup">{{ isMore ? '加载中...' : '目前就这么多了' }}</div>
            </div>
            <!-- 数据为空的时候 -->
            <div v-if="!isLoading && ((tabtype === 'publish' && publishList.length === 0) || (tabtype === 'join' && joinList.length === 0) || (tabtype === 'notice' && noticeList.length === 0))" class="my-list-empty">
              <pem-icon name="my-anonymous-empty" width="280" height="280"/>
              <p v-if="tabtype === 'publish'">快去发布吐槽信息吧，一大波圈内人在等您</p>
              <p v-if="tabtype === 'join'">圈内人都在玩吐槽，快去参与吐槽互动吧~</p>
              <p v-if="tabtype === 'notice'">暂无历史通知，去参与吐槽互动吧~</p>
              <pem-button @submit="goAnonymousList" text="去看看"/>
            </div>
          </div>
        </pem-scroll-view>
    </div>
    <div v-if="tabtype === 'notice' && noticeList.length > 0" @click="clearNotice" class="clear-notice">清空</div>
    <pem-tabs class="pem-tabs" :list="tabs" :tabtype="tabtype" @switchTab="switchTab"/>
    <pem-operation-panel @backtotop="backtotop" />
    <pem-loading v-show="isLoading"></pem-loading>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
    <pem-dialog ref='alert' :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemIcon from 'components/pem-icon/pem-icon'
import PemTabs from 'components/pem-tabs/pem-tabs'
import PemButton from 'components/pem-button/pem-button'
import PemAnonymousList from 'components/pem-anonymous-list/pem-anonymous-list'
import PemAnonymousNoreadList from 'components/pem-anonymous-noread-list/pem-anonymous-noread-list'
import PemToast from 'components/pem-toast/pem-toast'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { mapState, mapActions } from 'vuex'
import { MiniSiteApi, ImgPath } from 'api'
import { MY_ANONYMOUS_CLEAR, MY_ANONYMOUS_UPDATE } from 'store/minisite/my-anonymous'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemIcon,
    PemTabs,
    PemButton,
    PemAnonymousList,
    PemAnonymousNoreadList,
    PemToast,
    PemDialog,
    PemLoading
  },
  data () {
    return {
      currentIndex: 0,
      tabtype: 'publish',
      tabs: [
        {text: '我发布的', type: 'publish'},
        {text: '我参与的', type: 'join'},
        {text: '历史通知', type: 'notice'}
      ],
      listenList: [],
      publishList: [],
      joinList: [],
      noticeList: [],
      currentPublishPage: 1,
      currentJoinPage: 1,
      currentNoticePage: 1,
      pageSize: 10,
      toastText: '',
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isLoading: true,
      isMore: true
    }
  },
  created () {
    this.tabtype = (this.myAnonymous && this.myAnonymous.tabtype) ? this.myAnonymous.tabtype : 'publish'
    this._initWXShare()
    this._setTitle('项目工场-我的吐槽')
    setTimeout(() => {
      this._initData('init')
    }, 20)
  },
  computed: mapState({
    auth: state => state.auth,
    myAnonymous: state => state.myAnonymous
  }),
  beforeDestroy () {
    console.log('fdsf')
    let _myAnonymous = JSON.parse(JSON.stringify(this.myAnonymous))
    _myAnonymous.tabtype = this.tabtype
    switch (this.tabtype) {
      case 'publish':
        _myAnonymous.y1 = this.$refs.scroll.scroll.y
        break
      case 'join':
        _myAnonymous.y2 = this.$refs.scroll.scroll.y
        break
      case 'notice':
        _myAnonymous.y3 = this.$refs.scroll.scroll.y
        break
      default:
        break
    }
    this.MY_ANONYMOUS_UPDATE(_myAnonymous)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData (type = 'init') {
      if (type === 'init') this.backtotop()
      switch (this.tabtype) {
        case 'publish':
          this.listenList = this.publishList
          this.getMyPublishList(type)
          break
        case 'join':
          this.listenList = this.joinList
          this.getMyJoinList(type)
          break
        case 'notice':
          this.listenList = this.noticeList
          this.getMyNoticeList(type)
          break
        default:
          break
      }
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    switchTab (type) {
      this.tabtype = type
      this.isMore = true
      this.listRefresh()
      this.$nextTick(function () {
        this.$refs.scroll.refresh()
      })
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        let _myAnonymous = JSON.parse(JSON.stringify(this.myAnonymous))
        switch (this.tabtype) {
          case 'publish':
            _myAnonymous.publishList = []
            _myAnonymous.currentPublishPage = 1
            _myAnonymous.totalPublishPage = 1
            _myAnonymous.y1 = 0
            break
          case 'join':
            _myAnonymous.joinList = []
            _myAnonymous.currentJoinPage = 1
            _myAnonymous.totalJoinPage = 1
            _myAnonymous.y2 = 0
            break
          case 'notice':
            _myAnonymous.noticeList = []
            _myAnonymous.currentNoticePage = 1
            _myAnonymous.totalNoticePage = 1
            _myAnonymous.y3 = 0
            break
          default:
            break
        }
        this.MY_ANONYMOUS_UPDATE(_myAnonymous)
        this._initData('init')
      }
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this._initData('more')
      }
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    getMyPublishList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPublishPage = 1
      }
      if (this.myAnonymous &&
        this.myAnonymous.publishList &&
        this.myAnonymous.publishList.length > 0 &&
        this.currentPublishPage <= this.myAnonymous.currentPublishPage) {
        this.isLoading = false
        this.currentPublishPage = this.myAnonymous.currentPublishPage + 1
        if (+this.myAnonymous.totalPublishPage <= +this.myAnonymous.currentPublishPage) {
          this.isMore = false
        }
        this.publishList = this.myAnonymous.publishList
        this.listenList = this.publishList
        if (type === 'init') {
          setTimeout(() => {
            this.$refs.scroll.scrollTo(0, this.myAnonymous.y1 || 0)
          }, 21)
        }
        return false
      }
      this._myAnonymousPublish({
        currentPage: +this.currentPublishPage,
        pageSize: +this.pageSize
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPublishPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.publishList
          _list = _list.concat(data.resultList || []).map(item => {
            item.image = (item.image || []).map(imgsrc => ImgPath + imgsrc.replace(ImgPath, ''))
            return item
          })
          let _myAnonymous = JSON.parse(JSON.stringify(this.myAnonymous))
          _myAnonymous.publishList = _list
          _myAnonymous.currentPublishPage = +data.currentPage || 1
          _myAnonymous.totalPublishPage = +data.totalPage
          this.MY_ANONYMOUS_UPDATE(_myAnonymous)
          this.publishList = _list
          this.listenList = this.publishList
          this.$nextTick(function () {
            this.$refs.scroll.refresh()
          })
        }
      })
    },
    getMyJoinList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentJoinPage = 1
      }
      if (this.myAnonymous &&
        this.myAnonymous.joinList &&
        this.myAnonymous.joinList.length > 0 &&
        this.currentJoinPage <= this.myAnonymous.currentJoinPage) {
        this.isLoading = false
        this.currentJoinPage = this.myAnonymous.currentJoinPage + 1
        if (+this.myAnonymous.totalJoinPage <= +this.myAnonymous.currentJoinPage) {
          this.isMore = false
        }
        this.joinList = this.myAnonymous.joinList
        this.listenList = this.joinList
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.myAnonymous.y2 || 0)
        }, 21)
        return false
      }
      this._myAnonymousJoin({
        currentPage: +this.currentJoinPage,
        pageSize: +this.pageSize
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentJoinPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.joinList
          _list = _list.concat(data.resultList || []).map(item => {
            item.image = (item.image || []).map(imgsrc => ImgPath + imgsrc.replace(ImgPath, ''))
            return item
          })
          let _myAnonymous = JSON.parse(JSON.stringify(this.myAnonymous))
          _myAnonymous.joinList = _list
          _myAnonymous.currentJoinPage = +data.currentPage || 1
          _myAnonymous.totalJoinPage = +data.totalPage
          this.MY_ANONYMOUS_UPDATE(_myAnonymous)
          this.joinList = _list
          this.listenList = this.joinList
          this.$nextTick(function () {
            this.$refs.scroll.refresh()
          })
          setTimeout(() => {
            this.$refs.scroll.refresh()
          }, 100)
        }
      })
    },
    getMyNoticeList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentNoticePage = 1
      }
      if (this.myAnonymous &&
        this.myAnonymous.noticeList &&
        this.myAnonymous.noticeList.length > 0 &&
        this.currentNoticePage <= this.myAnonymous.currentNoticePage) {
        this.isLoading = false
        this.currentNoticePage = this.myAnonymous.currentNoticePage + 1
        if (+this.myAnonymous.totalNoticePage <= +this.myAnonymous.currentNoticePage) {
          this.isMore = false
        }
        this.noticeList = this.myAnonymous.noticeList
        this.listenList = this.noticeList
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.myAnonymous.y3 || 0)
        }, 21)
        return false
      }
      this._myAnonymousNotice({
        currentPage: +this.currentNoticePage,
        pageSize: +this.pageSize
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentNoticePage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.noticeList
          _list = _list.concat(data.resultList || []).map(item => {
            item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
            item.image = (typeof item.image === 'string') ? (ImgPath + item.image.replace(ImgPath, '')) : ''
            return item
          })
          let _myAnonymous = JSON.parse(JSON.stringify(this.myAnonymous))
          _myAnonymous.noticeList = _list
          _myAnonymous.currentNoticePage = +data.currentPage || 1
          _myAnonymous.totalNoticePage = +data.totalPage
          this.MY_ANONYMOUS_UPDATE(_myAnonymous)
          this.noticeList = _list
          this.listenList = this.noticeList
          this.$nextTick(function () {
            this.$refs.scroll.refresh()
          })
        }
      })
    },
    clearNotice () {
      this.isLoading = true
      this._myAnonymousNoticeClear(data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.noticeList = []
          this.listenList = []
        } else {
          this.toast('清空失败')
        }
      })
    },
    goAnonymousList () {
      this.$router.push('/anonymous-list')
    },
    _myAnonymousPublish (options, callback) {
      MiniSiteApi.myAnonymousPublish(options).then(data => {
        callback(data)
      })
    },
    _myAnonymousJoin (options, callback) {
      MiniSiteApi.myAnonymousJoin(options).then(data => {
        callback(data)
      })
    },
    _myAnonymousNotice (options, callback) {
      MiniSiteApi.myAnonymousNotice(options).then(data => {
        callback(data)
      })
    },
    _myAnonymousNoticeClear (callback) {
      MiniSiteApi.myAnonymousNoticeClear().then(data => {
        callback(data)
      })
    },
    ...mapActions([
      MY_ANONYMOUS_UPDATE,
      MY_ANONYMOUS_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .my-anonymous
    font-size: 0.24rem
    .page-panel
      top: 0.89rem
      &.page-panel-clear
        bottom: 0.95rem
    .my-list-empty
      padding-top: 0.94rem
      text-align: center
      p
        padding: 0 1.52rem 0.5rem
        font-size: 0.3rem
        color: #41403E
        line-height: 0.42rem
      .pem-submit
        display: inline-block
        width: 3rem
    .pem-tabs
      position: fixed
      top: 0
      left: 0
      right: 0
      z-index: 100
      background: #fff 
    .clear-notice
      position: fixed
      bottom: 0
      left: 0
      width: 100%
      height: 0.94rem
      line-height: 0.94rem
      font-size: 0.3rem
      color: #3F3F3F
      background: #FBFBFB
      text-align: center
      border-top: 0.02rem solid #f3f3f3
</style>



